<template>
  <div class="resume-select">
    <field
      class="resume-select__field"
      :label="title"
      :placeholder="currentValue"
      readonly
      @click="isShowSelect=true"
    />
    <cover v-if="isShowSelect">
      <div
        class="resume-select__select">
        <picker
          @change="handleChange"
          @cancel="hideShowSelect"
          @confirm="hideShowSelect"
          show-toolbar 
          :columns="columns" />
      </div>
    </cover>
  </div>  
</template>


<script>
import { Field, Picker } from "vant";
import cover from "./cover";
export default {
  data() {
    return {
      isShowSelect: false
    };
  },
  model: {
    prop: "selectValue",
    event: "changeSelectValue"
  },
  computed: {
    currentValue: {
      get() {
        return this.columns[this.selectValue];
      },
      set(value) {}
    }
  },
  props: {
    selectValue: {
      type: [String, Number],
      default: 0
    },
    title: {
      type: String,
      required: true
    },
    columns: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    hideShowSelect() {
      this.isShowSelect = false;
    },
    handleChange(_, value, index) {
      console.log(index);
      this.$emit("changeSelectValue", index);
    }
  },
  components: {
    Field,
    Picker,
    cover
  }
};
</script>


<style lang="postcss" scoped>
.resume-select {
  &__field {
    >>> input::-webkit-input-placeholder {
      color: #000;
    }
  }
  &__select {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }
}
</style>



