<template>
  <div class="resume-new-generate">
    <cell-group class="resume-new-generate__item">
      <cell title="头像" center>
        <uploader>
          <img class="upload-img" src="/resume-card/static/image/avatar.jpeg"/>
        </uploader>
      </cell>
      <field
        label="姓名"
        placeholder="请输入"
      />
      <my-select 
        v-model="sex"
        title="性别" 
        :columns="sexColumns"/>
      <my-select 
        v-model="trade"
        title="行业" 
        :columns="tradeColumns"/>
      <field
        label="公司"
        placeholder="正确完善企业可高端扩列"
      />
      <my-select 
        v-model="profession"
        title="职业" 
        :columns="professionColumns"/>
      <my-select 
        v-model="job"
        title="职务"
        :columns="jobColumns"/>
    </cell-group>

    <div class="resume-new-generate__button">
      <my-button size="large" type="primary">生成名片</my-button>
    </div>
  </div>  
</template>


<script>
import MySelect from "@/components/select";
import { Cell, CellGroup, Field, Uploader, Button } from "vant";
export default {
  data() {
    return {
      company: "",
      busines: "",
      startTime: "",
      sex: 0,
      trade: 0,
      profession: 0,
      job: 0,

      // status
      isShowStartTime: false,

      // settings value
      sexColumns: ["请选择", "男", "女"],
      tradeColumns: ["请选择", "电力|工业", "IT|互联网|游戏"],
      professionColumns: ["请选择", "研发", "产品"],
      jobColumns: ["请选择", "产品经理", "前端开发"]
    };
  },
  components: {
    CellGroup,
    Cell,
    Field,
    Uploader,
    MySelect,
    MyButton: Button
  },
  methods: {
    hideStartTime() {
      this.isShowStartTime = false;
    }
  }
};
</script>

<style lang="postcss" scoped>
.resume-new-generate {
  overflow-x: hidden;
  overflow-y: auto;
  &__item {
    margin-top: 10px;
    .upload-img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
  }
  &__button {
    margin: 25px 12.5px 0;
  }
}
</style>



